import React from 'react';
import styled from 'styled-components';
import { withRouter } from 'react-router';
import { FormattedMessage } from 'react-intl';
import { Col1_3, Col2_3 } from '../hoc/Grid';

const ScheduleButton = styled.button`
  font-size: 1.4rem;
  margin-left:0px;
  padding-left:0;
  display:block;
  border-width:0px;
`;

const Therapist = (props) => {
  return (
    <div className="col-full-half no-right-margin default-left-margin" onClick={() => { props.goToDetails() }}>
      <Col1_3 classes="no-right-margin">
        <img src={props.imageUrl} className="avatar avatar-lg" />
      </Col1_3>
      <Col2_3 classes="no-right-margin">
        <div>
          <div className="name-title">{props.name}</div>
          <label>{props.qualifications}</label>
          <label className="qoute">{props.qoute}</label>
          <ScheduleButton className="btn primary-text" onClick={() => { props.goToDetails() }}>
            <FormattedMessage id="therapists.chose" />
          </ScheduleButton>
        </div>
      </Col2_3>
    </div>
  );

}

export default withRouter(Therapist);