import React from 'react';
import '../../styles/main.css'
import { FormattedMessage } from 'react-intl';

const input = (props) => {
  let inputElement = null;
  console.log(props);

  let label = <label><FormattedMessage id={props.label} /></label>
  switch (props.elementType) {
    case ('input'):
      inputElement =
        <div className="Input">
          {label}
          <input onChange={props.changed}
            className="InputElement form-control" {...props.elementConfig} value={props.value} /></div>;
      break;
    case ('textarea'):
      inputElement = <div className="Input">{label}<textarea
        onChange={props.changed}
        className="InputElement form-control" {...props.elementConfig} /></div>;
      break;

    case ('checkbox'):
      inputElement = <div className="Input">
        {label}<input type="checkbox" /></div>
      break;

    case ('radio'):
      inputElement = <div className="Input">
        {label}<input type="radio" /></div>

      break;

    case ('button-next'):
      inputElement =
        <div className="Input">
          <button className="btn-primary-outline" onClick={props.changed} type="button">Next
      <div className="float-right icon-primary">
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" className="injected-svg bg-primary" data-src="assets/img/icons/interface/icon-arrow-right.svg">
                <path d="M12 5L19 12L12 19M5 12H18H5Z" stroke="#2C3038" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"></path>
              </svg>
            </div>
          </button >
        </div>

      break;
    case ('button-submit'):
      inputElement =
        <div className="Input">
          {/* Buttons without the type="button" will submit the form -> type="submit" will submit the form */}
          <button className='Input btn-primary' onClick={props.changed} type="button"> Submit </button>
        </div>

      break;

    case ('select'):
      inputElement = (
        <div className="Input">
          {label}
          <select
            onChange={props.changed}
            className='InputElement custom-select dropdown form-control'>
            {props.elementConfig.options.map(element => (
              <FormattedMessage id={element} defaultMessage="" tagName="option" value={element} />
            )
            )}
          </select>
        </div >
      )
  }

  return (

    <div className="Input">
      {inputElement}
    </div>
  );

}

export default input;
