import React, { Component } from 'react';
import Input from '../Input/Input.js'
import '../../styles/main.css'
import formData from '../../resources/form.json';

//axios
import axios from 'axios';
import { FormattedHTMLMessage } from 'react-intl';
import Page from '../hoc/Page.js';
import Section from '../hoc/Section.js';
import { Row } from '../hoc/Grid.js';

axios.defaults.baseURL = 'https://uefqb4kxmk.execute-api.eu-central-1.amazonaws.com/default/';
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
// axios.defaults.headers.common['Access-Control-Allow-Origin'] = "localhost";
// axios.defaults.headers.common["Access-Control-Allow-Origin"] = "*"; // Required for CORS support to work
// axios.defaults.headers.common["Access-Control-Allow-Credentials"] = "true";
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
// axios.defaults.withCredentials = true;
axios.defaults.crossDomain = true;

// Components that manage state and presentation components that do not manage state - dumb compoenents
// Only few compoenents that manage state -> Container componenets

// extends Compoenent has state by default!!
// if (process.env.NODE_ENV !== 'production') {
//     const { whyDidYouUpdate } = require('why-did-you-update');
//     whyDidYouUpdate(React);
// }

class Form extends Component {

    sendFormData = async () => {
        try {
            const { data } = await axios.post('formSaveToDatabase', { data: this.state })
            return data;
        } catch (err) {
            console.log(err.message);
        }
    }

    shouldComponentUpdate(old, newP) {
        console.log("should update");
        return false;
    }

    inputChangedHandler = (event, inputKey, type, page) => {
        // Save the data
        console.log(type);
        console.log(page);
        console.log(inputKey);

        console.log("Next!!!!");
        console.log(type);

        switch (type) {
            case ('button-next'):
                // Load next page

                console.log(this.state)
                console.log(parseInt(page) + 1)
                let nextPage = parseInt(page) + 1
                this.props.history.push('/form/' + nextPage, { state: this.state })
                break;
            case ('button-submit'):
                // https://www.pluralsight.com/guides/all-need-to-know-about-axios
                console.log("Next!!!!")
                // React.useEffect(() => {
                axios.post('formSaveToDatabase', { data: this.state }).then(response => this.redirect())
                    .catch(err => {
                        console.log(err.message);
                    })
                break;
            default:
                // Save the values
                const updatedForm = this.state
                const updatedFormElement = { ...this.state[inputKey] }
                console.log(updatedFormElement)
                updatedFormElement.value = event.target.value
                updatedForm[inputKey] = updatedFormElement
                console.log(updatedFormElement)
                this.setState(updatedForm)
                break;

        }
    }

    redirect = () => {
        this.props.history.push({
            pathname: '/success'
        })
    }

    componentDidMount() { }

    render() {
        if (this.state != null && this.state.redirect) {
            console.log("Redirect")
            this.redirect()
            return
        }
        const { id } = this.props.match.params
        console.log(id)
        let formArray = [];

        let page = parseInt(id) || 0
        let formArray2 = [];
        // console.log(formData)

        const pageInfo = formData[page];
        const title = pageInfo.stepTitle;
        const subtitle = pageInfo.stepSubtitle;

        let index = 0
        let state = this.state ?? {}
        pageInfo.stepInputs.map(element => {
            // console.log(element)
            let id = index + page * 10;
            let value = ""
            if (this.state != null && this.state[id] != null) {
                value = this.state[id].value ?? ""
            }
            let elementInfo = {
                id: id,
                info: element,
                value: value
            }
            state[id] = elementInfo
            formArray.push(elementInfo)
            index += 1
        })

        this.state = {
            state,
            redirect: false
        }
        console.log(state)

        return (
            <Page>
                <Section>
                    <Row classes="largeTitle">
                        <h2>
                            <mark><FormattedHTMLMessage id={title}></FormattedHTMLMessage></mark>
                        </h2>
                        <p className="subtitle"><FormattedHTMLMessage id={subtitle}></FormattedHTMLMessage></p>
                    </Row>
                    <Row >
                        <div className="center-in-parent">
                            <form onSubmit={this.onSubmit}>
                                {formArray.map(element => (
                                    <Input
                                        changed={(event) => this.inputChangedHandler(event, element.id, element.info.type, page)}
                                        elementType={element.info.type} elementConfig={element.info.config} label={element.info.name} key={element.id} />
                                ))}
                            </form >
                        </div >
                    </Row>
                </Section>
            </Page>
        );
    }
}

export default Form;