import React, { Component } from 'react';
import '../styles/main.css'
import Header from '../components/Header/Header.js'

import { Authenticator, SignIn } from 'aws-amplify-react';
import Amplify, { Auth, Hub } from 'aws-amplify';
import { withFederated } from 'aws-amplify-react';
import { Route, Link } from 'react-router-dom';
import Form from '../components/Form/Form.js';

import { withRouter } from 'react-router';
import Home from './Home';
import TherapistsList from './List.js';
import SuccessPage from './Success';
import FAQPage from './FAQ';
import TherapistDetails from '../components/Therapists/TherapistsDetails'
import Footer from '../components/Footer/Footer';

Amplify.configure({
  Auth: {
    mandatorySignIn: true,
    region: 'milahealth',
    userPoolId: 'eu-central-1_hLQPxgxW6',
    // identityPoolId: 'eu-central-1:a040f95e-cf2b-4a6c-81c2-4fbb7e504d2b',
    userPoolWebClientId: 'qqcp6rjg3q9e2351hem1meneg',
    // OPTIONAL - Hosted UI configuration
    oauth: {
      domain: 'milahealth.auth.eu-central-1.amazoncognito.com',
      scope: ['email', 'openid'],
      redirectSignIn: 'http://localhost:3000/',
      redirectSignOut: 'http://localhost:3000/',
      responseType: 'token' // or 'token', note that REFRESH token will only be generated when the responseType is code
    }
  }
});

class AppComponent extends Component {
  state = { user: null, customState: null };

  componentDidMount() {
    Hub.listen("auth", ({ payload: { event, data } }) => {
      console.log(event);
      switch (event) {

        case "signIn":
          console.log('now the user is signed in');
          const user = data;
          console.log(user);
          this.setState({ user: data });
          console.log(this.state.user);
          this.navigateToHomePage();
          break;
        case "signOut":
          this.setState({ user: null });
          break;
        case "customOAuthState":
          this.setState({ customState: data });
      }
    });

    Auth.currentAuthenticatedUser()
      .then(user => {
        this.setState({ user: user })
        if (user != null) {
          this.navigateToHomePage();
        } else {

        }
        console.log(this.state.user);
      })
      .catch(() => {
        // // navigate to home page
        // window.location.href = '/static/index.html'
        console.log("Not signed in")
      }
      );
  }

  loginOrSignOut = () => {
    console.log(this.state.user)
    if (this.state.user == null) {
      Auth.federatedSignIn()
    } else {
      Auth.signOut()
    }
  }

  navigateToHomePage = () => {
    this.props.history.push({ pathname: '/home' })
  }

  render() {
    return (
      <div className="block-width">
        <Header customTab={this.state.user == null ? 'Login' : 'Sign Out'} callback={this.loginOrSignOut} />
        <Route path='/' exact component={Home} />
        <Route path="/home" component={Home} />
        <Route path="/form" exact component={Form} />
        <Route path="/form/:id" render={(props) => <Form {...props} />} />
        <Route path="/list" exact component={TherapistsList} />
        <Route path="/details" exact component={TherapistDetails} />
        <Route path="/success" exact component={SuccessPage} />
        <Route path="/faq" exact component={FAQPage} />
        {/* <Route path="/grid" exact component={Grid} /> */}
        <Footer />
      </div>
    );
  }
}

export default withRouter(AppComponent);
