import React, { Component } from 'react';
import { withRouter } from 'react-router';
import Therapist from '../components/Therapists/Therapist';
import styled from 'styled-components';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';

import myData from './../resources/therapists.json';
import Section from '../components/hoc/Section';
import Page from '../components/hoc/Page';
import { Row } from '../components/hoc/Grid';

const language = navigator.language.split(/[-_]/)[0];

class TherapistsList extends Component {

    state = {
        list: myData
    }

    goToDetails(props) {
        console.log(props);
        this.props.history.push("/details", { state: props });
    }

    render() {
        return (
            <Page>
                <Section>
                    <Row>
                        <div className="largeTitle">
                            <h2 > <mark><FormattedHTMLMessage id="therapists_list.title" /> </mark></h2>
                            <p className="subtitle"><FormattedMessage id="therapists_list.subtitle" /></p>

                        </div>
                    </Row>
                    <Row>
                        {this.state.list.map(element => (
                            <Therapist
                                key={element.name}
                                name={element.name}
                                qoute={element[language].qoute}
                                qualifications={element[language].title}
                                imageUrl={element.picture_url}
                                goToDetails={() => { this.goToDetails(element) }} />
                        ))}
                    </Row>

                </Section >
            </Page>
        );
    }
}

export default withRouter(TherapistsList);