import React, { Component } from 'react';
import '../styles/main.css'
import { withRouter } from 'react-router';
import { FormattedMessage, FormattedHTMLMessage } from 'react-intl';
import Page from '../components/hoc/Page';
import Section from '../components/hoc/Section';
import { Row, Col2_3, Col1_3 } from '../components/hoc/Grid';


class Home extends Component {

    navigateToForm = () => {
        this.props.history.push({ pathname: '/form' })
    }

    navigateToTherapistsList = () => {
        this.props.history.push({ pathname: '/list' })
    }

    render() {
        // Declare the icons and the text in an array and make it dynamic

        return (
            <Page>
                <Section >
                    <Row>
                        <Col2_3 classes="largeTitle">
                            <h2 >
                                <mark > <FormattedMessage id="home.title" /></mark>
                            </h2>
                            <p className="subtitle"><FormattedHTMLMessage id="home.subtitle" /></p>

                        </Col2_3>
                        <Col1_3>
                            <img className="avatar avatar-title-left avatar-lg" src="https://mila-resources.s3.eu-central-1.amazonaws.com/raj-rana-15Vb4B_ma_s-unsplash.jpg" />
                        </Col1_3>
                    </Row>
                    <Row>
                        <div className="cardHome" onClick={this.navigateToForm}>
                            <h4 className="mt-4"><FormattedMessage id="home.start" /></h4>
                            <div><FormattedMessage id="home.start_subtext" /></div>
                        </div>
                        <div className="cardHome" onClick={this.navigateToTherapistsList}>
                            <h4 className="mt-4"><FormattedMessage id="home.already_here" /></h4>
                            <div><FormattedMessage id="home.already_here_subtext" /></div>
                        </div>
                    </Row>
                </Section>
            </Page>
        );
    }
}

export default withRouter(Home);